import Image from 'mui-image'

import ChatBot from './ChatBot';

const drawerWidth=240;
// const navItems = ["Work","Method","Services","Company","Contact"]
const navItems =[
    {
        key:1,
        text:"Home",
        link:"/",
        endIcon:null
    },
    {
        key:2,
        text:"Method",
        link:"/method",
        endIcon:null
    },
    {
        key:3,
        text:"Our Products",
        link:null,
        endIcon:<KeyboardArrowDownIcon/>
    },
    {
        key:4,
        text:"Services",
        link:"/services",
        endIcon:null
    },
    {
        key:5,
        text:"Company",
        link:null,
        endIcon:<KeyboardArrowDownIcon/>
    },
    {
        key:6,
        text:"Contact Us",
        link:"/contact",
        endIcon:null
    },
    {
        key:7,
        text:"Start a Project",
        link:"/",
        endIcon:null
    }
]


const theme = createTheme({
    palette:{
        primary:{
            main:"#fff"
        },
        secondary:{
            main:"#369DC8"
        }
    },
    typography:{
        main:"Noto  serif",
        

    }
})

const products = [
    {
        key:1,
        text:"ChatBot",
        link:"/chatbot"
    },
    {
        key:2,
        text:"NeuralWeb",
        link:"/neuralweb"
    },
    {
        key:3,
        text:"Tesys.in",
        link:"/tesys"
    }
]


// about submenu
const about = [
    {
        key:1,
        text:'Our Company',
        link:"/company"
    },
    {
        key:2,
        text:'Our Team',
        link:"/team"
    },
    {
        key:3,
        text:'Career',
        link:"/career"
    },
    {
        key:4,
        text:'Insights',
        link:"/"
    }

  ]

const Navbar = (props) => {
   
    const [mobileOpen, setMobileOpen] = React.useState(false);

   
    const handleDrawerToggle =()=>{
        setMobileOpen((prevState) => !prevState);
    };
    

   
    
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [anchorElProducts, setAnchorElProducts] = React.useState(null);

    const open = Boolean(anchorEl)
    const openProducts = Boolean(anchorElProducts)

    const handleClick = (event) => {
        // console.log("cT:- ", event.currentTarget['text'])
        if (event.currentTarget['text'] === "Company"){
          setAnchorEl(event.currentTarget);
        }
        if (event.currentTarget['text'] !== "Company")
        {
          setAnchorEl(null);
        }
        if (event.currentTarget['text'] === "Our Products"){
            setAnchorElProducts(event.currentTarget);
        }
        if(event.currentTarget["text"] !== "Our Products"){
            setAnchorElProducts(null)
        }
      };

      
    const handleClose = (event) => {
        setAnchorEl(null);
        setAnchorElProducts(null);
      };


    const drawer= (
        <Box
             sx={{textAlign: "center",}}
        >
            <Typography
                variant="h6"
                sx={{my:2,fontFamily:"Noto sans",fontSize:20}}
            ><a href="#">
                <Image src="/uploads/images/logo.png" sx={{pl:5,pr:5}}></Image>
            </a>
            </Typography>
            <Divider/>
            <List>
                <ListItem key="1" disablePadding >
                    <ListItemButton sx={{color:"#000"}} to="/">
                        <Typography sx={{fontFamily:"Noto sans", color:"#004AAD"}}>Home</Typography>
                    </ListItemButton>
                </ListItem>
                <ListItem key="2" disablePadding>
                    <ListItemButton sx={{color:"#000"}} to="/method">
                        <Typography sx={{fontFamily:"Noto sans"}}>Method</Typography>
                    </ListItemButton>
                </ListItem>
                <Accordion elevation={0}>
                    <AccordionSummary 
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        >
                        <Typography sx={{fontFamily:"Noto sans"}}   >Our Products</Typography>
                    </AccordionSummary>
                    <AccordionDetails sx={{backgroundColor:"#004AAD"}}>
                    <ListItem key="4.1" disablePadding>
                            <ListItemButton sx={{ color:"#fff"}} to="/chatbot"> 
                                <Typography sx={{fontFamily:"Noto sans"}}>Chat Bot</Typography>
                            </ListItemButton>
                        </ListItem> 
                        <ListItem key="4.2" disablePadding>
                            <ListItemButton sx={{ color:"#fff"}} to="/neuralweb"> 
                                <Typography sx={{fontFamily:"Noto sans"}}>Neuralweb</Typography>
                            </ListItemButton>
                        </ListItem>
                     </AccordionDetails>
                </Accordion>
                <ListItem key="3" disablePadding>
                    <ListItemButton sx={{ color:"#000"}} to="/services">
                        <Typography sx={{fontFamily:"Noto sans"}}>Services</Typography>
                    </ListItemButton>
                </ListItem>
                <Accordion elevation={0}>
                    <AccordionSummary 
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        >
                        <Typography sx={{fontFamily:"Noto sans"}}   >Company</Typography>
                    </AccordionSummary>
                    <AccordionDetails sx={{backgroundColor:"#004AAD"}}>
                    <ListItem key="4.1" disablePadding>
                            <ListItemButton sx={{ color:"#fff"}} to="/company"> 
                                <Typography sx={{fontFamily:"Noto sans"}}>Our Company</Typography>
                            </ListItemButton>
                        </ListItem> 
                        <ListItem key="4.2" disablePadding>
                            <ListItemButton sx={{ color:"#fff"}} to="/team"> 
                                <Typography sx={{fontFamily:"Noto sans"}}>Our Team</Typography>
                            </ListItemButton>
                        </ListItem>
                        <ListItem key="4.3" disablePadding>
                            <ListItemButton sx={{ color:"#fff"}} to="/career">      
                                <Typography sx={{fontFamily:"Noto sans"}}>Career</Typography>
                            </ListItemButton>
                        </ListItem>
                    </AccordionDetails>
                </Accordion>
                <ListItem key="5" disablePadding>
                    <ListItemButton sx={{ color:"#000"}} to="/contact">
                        <Typography sx={{fontFamily:"Noto sans"}}>Contact Us</Typography>
                    </ListItemButton>
                </ListItem> 
                <ListItem key="6" disablePadding>
                    <ListItemButton sx={{ color:"#000"}} to="/">
                        <Typography sx={{fontFamily:"Noto sans"}}>Start a Project</Typography>
                    </ListItemButton>
                </ListItem>

              
           
            </List>
        </Box>
    )

    // const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <ThemeProvider theme={theme}>
        <Box sx={{display:"flex"}}>
            <CssBaseline/>
            <AppBar position="static" sx={{p:1,}}>
                <Container>
                    <Toolbar>
                        <IconButton
                            color="inherit"
                            aria-label='open drawer'
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{mr:2, display:{ md: 'none' }}}
                        >
                            <MenuIcon></MenuIcon>
                        </IconButton>
                        <Typography
                            variant="h5"
                            component="div"
                            sx={{flexGrow:1, display:{xs:"block", sm:"block", fontWeight:"bold"}}}
                        >
                            <a href="/">
                                <Image src="/uploads/images/logo.png" width="140px"/>
                            </a>
                        </Typography>
                        <Box
                            sx={{display:{xs:"none",sm:"none", md:"block"}}}
                        >
                            {navItems.map((item)=>(
                                <Button 
                                    key={item.key} 
                                    sx={{color:"#004AAD", mr:2, fontFamily:"Noto serif",textTransform: 'none', border:item.key===7?"1px solid #004AAD":"none", borderRadius:item.key===7?100:0, "&:hover":{bgcolor:item.key===7?"#004AAD":"", color:item.key===7?"#fff":"none"}}} 
                                    component={Link} 
                                    to={item.link}
                                    onClick={handleClick}
                                    
                                    >
                                          {item.text}
                                     
                                    <IconButton edge="end" aria-label={item.endIcon} sx={{color:"#004AAD"}}>{item.endIcon}</IconButton>
                                </Button>
                            ))}
                            {/* About Submenu */}
                            <Menu
                            id="fade-menu"
                            MenuListProps={{
                                'aria-labelledby': 'fade-button',
                            }}
                            anchorEl={anchorEl}
                            open={open}
                            onClose={handleClose}
                            TransitionComponent={Fade}
                            
                            >
                            {about.map((item, key)=>(
                                <MenuItem 
                                key={key} 
                                component={Link} 
                                to={item.link} 
                                onClick={handleClose}
                                sx={{fontFamily:"Noto serif",backgroundColor:"#004AAD", fontSize:"13px",color:"#fff",mt:-1,mb:-1,p:2,transition:"0.8s all",'&:hover':{backgroundColor:"#fff", color:"#000", transition:"0.8s all"}}}
                                >{item.text}</MenuItem>
                            ))}
                            </Menu>
                            
                            {/* Products Submenu */}
                            <Menu
                            id="fade-menu"
                            MenuListProps={{
                                'aria-labelledby': 'fade-button',
                            }}
                            anchorEl={anchorElProducts}
                            open={openProducts}
                            onClose={handleClose}
                            TransitionComponent={Fade}
                            
                            >
                            {products.map((item, key)=>(
                                <MenuItem 
                                key={key} 
                                component={Link} 
                                to={item.link} 
                                onClick={handleClose}
                                sx={{fontFamily:"Noto serif",backgroundColor:"#004AAD", fontSize:"13px",color:"#fff",mt:-1,mb:-1,p:2,transition:"0.8s all",'&:hover':{backgroundColor:"#fff", color:"#000", transition:"0.8s all"}}}
                                >{item.text}</MenuItem>
                            ))}
                            </Menu>
                        </Box>
                        
                    </Toolbar>
                    
                </Container>
            </AppBar>


            <Box
                component="nav"
            >
                <Drawer
                    // container={container}
                    variant="temporary"
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    ModalProps={{
                        keepMounted:true,
                    }}
                    sx={{
                        display:{xs:"block", sm:"none"},
                        '& .MuiDrawer-paper':{boxSizing: 'border-box', width:drawerWidth},
                    }}
                >
                    {drawer}
                </Drawer>

            </Box>
           {/* <ChatBot/> */}
        </Box>
   </ThemeProvider>
  )
}

export default Navbar