const Contact = () => {

  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [companyName, setCompanyName] = useState('');
  const [message, setMessage] = useState('');

  const [isValidEmail, setIsValidEmail] = useState(true);
  const [isValidName, setIsValidName] = useState(true);
  const [isValidCompanyName, setIsValidCompanyName] = useState(true);
  const [isValidMessage, setIsValidMessage] = useState(true);

  const emailChange = (event)=>{
    setEmail(event.target.value)
    setIsValidEmail(true)
  }
  const nameChange = (event) =>{
    setName(event.target.value)
    setIsValidName(true)
  }
  const companyNameChange = (event) =>{
    setCompanyName(event.target.value)
    setIsValidCompanyName(true)
  }
  const messageChange = (event) => {
    setMessage(event.target.value)
    setIsValidMessage(true)
  }
  const submitRequest = async () =>{
    console.log("type of name:-",typeof(message))
    try{
      fetch(`http://aitessa.com/api/save/`,{
        method:"POST",
        body:JSON.stringify({
          name:name,
          email:email,
          companyName:companyName,
          additionalDetails:message
        }),
        headers:{
          'Content-Type': 'application/json; charset=UTF-8',
        }
      }).then(response=>response.json()).then(response=>console.log(response))
      
      setFinalModal(true)
    }catch(error){
      console.log(error)
    }
    
  }

  const checkValidation = () => {
    console.log('email = ', email)
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    let validationObj = {
      email: true,
      name: true,
      companyName:true,
      message:true
    }
    if(email.trim() === "" || !emailRegex.test(email)){
      validationObj.email = false;
    }
    if(name.trim() === ""){
      validationObj.name = false;
    }
    if(companyName.trim() === ""){
      validationObj.companyName = false;
    }
    if(message.trim() === ""){
      validationObj.message = false
    }
    if(Object.values(validationObj).indexOf(false) === -1){
      submitRequest();
    }else{
      setIsValidEmail(validationObj.email);
      setIsValidName(validationObj.name);
      setIsValidCompanyName(validationObj.companyName);
      setIsValidMessage(validationObj.message);
    }
  }

  //For Modal
  const [openModal, setOpenModal] = useState(false);
  const [finalModal, setFinalModal] = useState(false);
  const handleOpenModal = () => {
    setOpenModal(true)
  }
  const handleClose = () => {
    setOpenModal(false)
    setFinalModal(false)
  }
  
  const style = {
    position: 'relative',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 500,
    bgcolor: 'background.paper',
    
    borderRadius:"20px",
    boxShadow: 24,
    pt: 2,
    px: 4,
    pb: 3,
  };

  
  return (
    <>
    <Box bgcolor="#004AAD" color="white" sx={{flexGrow:1}} px={{xs:1, sm:5}} py={{xs:5, sm:10 }}>
      <Container maxWidth="md">

      <Grid container sx={{pt:{xs:10,sm:5},pb:{xs:10,sm:5}}}>
        <Grid item xs={12} sm={12} md={8} lg={8}>
          <Typography variant="h3" sx={{fontFamily:"Moto sans"}}>
            What can we help you achieve ?
          </Typography>
        </Grid>
        <Grid item xs={12} sm={12} md={4} lg={4}>
          <Button variant="contained" sx={{borderRadius:5,pl:5,pr:5,pt:1,pb:1,border:"1px solid #fff", fontFamily:"Moto serif", mt:{xs:5,sm:5}}} onClick={handleOpenModal}>
            Work with Us
          </Button>
          <Button variant="contained" component={Link} to="/career" sx={{borderRadius:5,pl:5,pr:5,pt:1,pb:1,border:"1px solid #fff", fontFamily:"Moto serif", mt:{xs:5,sm:5},ml:{xs:1}, bgcolor:"#fff", color:"#004AAD","&:hover":{color:"#004AAD",bgcolor:"#E2DFD2"}}}>
            Apply for Job
          </Button>
          <Modal open={openModal} onClose={handleClose}  aria-labelledby="parent-modal-title" aria-describedby="parent-modal-description">
            <Box sx={{ ...style, width: {xs:500,md:700}, bgcolor:"#004AAD",color:"#fff" }}>
           
             <Grid container sx={{pt:2,pb:2}} >
                <Grid xs={12} sm={6} md={6} lg={6} >
                <Image src="/uploads/images/contact.png" width="200px" height="200px"></Image>
                  <Typography variant="h5" sx={{mt:5,pr:5,fontFamily:"Moto sans"}}>We are here to help you.</Typography>
                  <Typography variant="p" component="p" sx={{fontFamily:"Moto sans",pr:5,mt:2}} >Aitessa, our dedicated personal assistant, is poised to facilitate seamless connections, ensuring your access to the right contacts with efficiency and precision.</Typography>
                  
                </Grid>
                <Grid xs={12} sm={6} md={6} lg={6}>
                <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:2}}>
                  <TextField 
                    placeholder='Name' 
                    fullWidth
                    label="Name"
                    InputProps={{style:{borderRadius:100,color:"#fff"}}} 
                    onChange={nameChange}
                    error={!isValidName}/>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:2}}>
                  <TextField 
                    placeholder='Email' 
                    label="email"
                    type='Email' 
                    fullWidth 
                    InputProps={{style:{borderRadius:100,color:"#fff"}}} 
                    onChange={emailChange}
                    error={!isValidEmail}/>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:2}}>
                  <TextField 
                    placeholder='Company Name'
                    label="Company Name"
                    fullWidth 
                    InputProps={{style:{borderRadius:100,color:"#fff"}}} 
                    onChange={companyNameChange}
                    error={!isValidCompanyName}/>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:2}}>
                  <TextField 
                    placeholder='Additional Details'
                    label="Additional Details" 
                    fullWidth 
                    multiline 
                    maxRows={3} 
                    InputProps={{style:{borderRadius:100,color:"#fff"}}} 
                    onChange={messageChange}
                    error={!isValidMessage}/>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:3}}>
                  <FormGroup sx={{fontFamily:"Moto sans"}}>
                  <FormControlLabel control={<CheckBox />} label="&nbsp;Yes i would like to recieve notification" sx={{fontFamily:"Moto sans"}}>
                    </FormControlLabel>
                    <FormControlLabel control={<CheckBox />} label="&nbsp;Accept Terms and Condition" sx={{fontFamily:"Moto sans"}}>
                    </FormControlLabel>
                  </FormGroup>
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12} sx={{pt:3}}>
                  <Button 
                    variant='contained' 
                    fullWidth 
                    sx={{bgcolor:"#004AAD",pt:1,pb:1,borderRadius:100,fontFamily:"Moto sans",textTransform:"none"}} 
                    onClick={checkValidation}>Submit</Button>
                </Grid>
              </Grid>
                </Grid>

             </Grid>
              
            </Box>
          </Modal>
          <Modal open={finalModal} onClose={handleClose} aria-labelledby="parent-modal-title" aria-describedby="parent-modal-description">
          <Box sx={{ ...style, width: {xs:500,md:500}, bgcolor:"#004AAD",color:"#fff",p:5 }}>
            <Typography variant="h6">Your Request is successfully Submitted. We will contact you soon.</Typography>
           </Box>
          </Modal>
        </Grid>

      </Grid>
      </Container>
    </Box>
    
    <Container>
    <Grid container sx={{mt:15,mb:10}}>
      <Grid item xs={12} sm={12} md={12} lg={12}>

      <Typography variant="h3" sx={{fontFamily:"Moto serif",mb:5}}>Direct Contact</Typography>
      </Grid>
    <Grid item xs={12} sm={6} md={4} lg={4}>
      <Typography variant="h5" fontFamily="Moto serif">Email</Typography>
      <Typography variant='p' fontFamily="Moto serif">tessaparmar@gmail.com</Typography>
    </Grid>
    <Grid item xs={12} sm={6} md={4} lg={4} >

      <Typography variant="h5" fontFamily="Moto serif" sx={{mt:{xs:10,sm:0}}}>Partners</Typography>
      <LinkedIn sx={{fontSize:30, verticalAlign:"middle",mr:1,color:"#004AAD"}}/><Typography variant='p' fontFamily="Moto serif" component={Link} to="https://www.linkedin.com/in/tessa-parmar01/" target="_blank">Tessa Parmar</Typography><br/>
      <LinkedIn sx={{fontSize:30, verticalAlign:"middle",mr:1,color:"#004AAD"}}/><Typography variant='p' fontFamily="Moto serif" component={Link} to="https://www.linkedin.com/in/shubh94/" target="_blank">Shubham Singh</Typography>

    </Grid>
    <Grid item xs={12} sm={6} md={4} lg={4}>
     <Typography variant="h5" fontFamily="Moto serif" sx={{mt:{xs:10,sm:0}}}>Website</Typography>
      <Typography variant='p' fontFamily="Moto serif">contact@aitessa.com</Typography><br/>
      
    </Grid>
    </Grid>
    </Container>
    
    <Container>
    <hr/>
    <Grid container sx={{mt:15,mb:10}}>
    
    <Grid item xs={12} sm={8} md={8} lg={8} sx={{pr:10}} >

     <Typography variant='p' fontFamily="Moto serif">Join Us on Social Media to hear our latest Insights, get a peek  at our culture and see how our unique approach helps clients achieve the extraordinary.</Typography><br/>
    
    </Grid>
    <Grid item xs={12} sm={6} md={4} lg={4}>
      <Link to="https://www.facebook.com" target="_blank"><Facebook sx={{fontSize:45,mr:2, color:"#004AAD"}}/></Link>
      <Link to="https://www.linkedin.com" target="_blank"><LinkedIn sx={{fontSize:45,mr:2, color:"#004AAD"}}/></Link>
      <Link to="https://www.instagram.com" target="_blank"><Instagram sx={{fontSize:45,mr:2, color:"#004AAD"}}/></Link>
      <Link to="https://www.twitter.com" target="_blank"><Twitter sx={{fontSize:45,mr:2, color:"#004AAD"}}/></Link>
      <Link to="https://www.youtube.com" target="_blank"><YouTube sx={{fontSize:50,mr:2, color:"#004AAD"}}/></Link>
    </Grid>
    </Grid>
    </Container>
    </>
    

  )
}

export default Contact